$wrap: 980px;

.feature {
  display: grid;
  grid-template-columns: minmax(260px, 42%) 1fr;
  align-items: stretch;
  gap: clamp(16px, 3vw, 28px);
  margin: clamp(16px, 3vw, 32px) 0;
  padding: clamp(14px, 2.6vw, 20px);
  border: 1px solid var(--border);
  border-radius: var(--rounded);
  background: var(--surface);
  box-shadow: var(--shadow);
  transition: transform var(--transition), box-shadow var(--transition),
    border-color var(--transition), background var(--transition);
}

.feature-image {
  width: 100%;
  min-height: 200px;
  aspect-ratio: 16 / 10;
  border: 1px solid var(--border);
  border-radius: var(--rounded-sm);
  overflow: hidden;
  background: var(--surface-alt);
}

.feature-image img {
  display: block;
  width: 100%;
  height: 100%;
  object-fit: var(--feature-image-fit, contain);
  object-position: var(--feature-image-position, 50% 50%);
  background: var(--surface-alt);
  transition: transform var(--transition);
}

.feature-news {
  padding: 8px;
}

.feature-news-items {
  display: grid;
  gap: 8px;
  align-content: start;
}

.feature-news-item {
  padding: 8px 10px;
  border: 1px solid var(--feature-news-item-border, var(--border));
  border-radius: var(--rounded-sm);
  background: var(--feature-news-item-bg, var(--surface-alt));
  box-shadow: inset 0 0 0 1px var(--feature-news-item-glow, transparent);
}

.feature-news-meta {
  display: flex;
  align-items: center;
  gap: 8px;
  margin-top: 4px;
}

.feature-news-link {
  color: var(--text);
  font-family: var(--heading);
  font-weight: var(--semi-bold);
  line-height: 1.35;
  text-decoration: none;
}

.feature-news-link:hover {
  color: var(--primary);
}

.feature-news-date {
  color: var(--gray);
  font-size: 0.84rem;
}

.feature-news-tag {
  display: inline-flex;
  align-items: center;
  padding: 2px 8px;
  border: 1px solid var(--primary-soft);
  border-radius: 999px;
  background: var(--primary-soft);
  color: var(--primary);
  font-size: 0.72rem;
  font-weight: var(--semi-bold);
  text-transform: uppercase;
  letter-spacing: 0.04em;
}

.feature-news-empty {
  margin: 0;
  color: var(--gray);
}

.feature-text {
  display: flex;
  justify-content: center;
  flex-direction: column;
  min-width: 0;
  text-align: left;
}

.feature:hover {
  transform: translateY(-2px);
  border-color: var(--primary-soft);
  background: var(--surface-alt);
  box-shadow: var(--shadow-strong);
}

.feature:hover .feature-image img {
  transform: none;
}

.feature-title {
  margin: 0 0 0.65rem !important;
  font-size: clamp(1.2rem, 2vw, 1.55rem);
  text-align: left;
  font-family: var(--heading);
  font-weight: var(--bold);
}

.feature[data-flip] {
  grid-template-columns: 1fr minmax(260px, 42%);
}

.feature[data-flip] .feature-image {
  order: 2;
}

.feature[data-flip] .feature-text {
  order: 1;
}

.feature.feature-image-right {
  grid-template-columns: 1fr minmax(260px, 42%);
}

.feature.feature-image-right .feature-image {
  order: 2;
}

.feature.feature-image-right .feature-text {
  order: 1;
}

.feature.feature-image-left {
  grid-template-columns: minmax(260px, 42%) 1fr;
}

.feature.feature-image-left .feature-image {
  order: 1;
}

.feature.feature-image-left .feature-text {
  order: 2;
}

@media (max-width: $wrap), (hover: none) and (pointer: coarse) {
  .feature {
    grid-template-columns: 1fr !important;
    padding: 12px;
  }

  .feature-image {
    min-height: 200px;
    max-width: 100%;
  }

  .feature-title {
    text-align: left;
  }
}

@media (max-width: 640px), (hover: none) and (pointer: coarse) {
  .feature {
    margin: 10px 0;
    padding: 10px;
    gap: 10px;
  }

  .feature-image {
    min-height: 118px;
  }
}
